  width: 17% !important;

  
  .news-swiper__top {
    .news-swiper__img {
    }
    span {
      font-size: 16px;
    }
    .btn-reset.news-swiper__icon {
      img {
      }
      &:hover {}
      &:active {}
      &:focus {}
    }
  }

  & .news-swiper__info {
