* {
  box-sizing: border-box;
}

*::after,
::before {
  box-sizing: inherit;
}

html {
  font-family: "Raleway", Arial, Helvetica, sans-serif;
}

body {
  position: relative;
  overflow-x: hidden;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 100vh;
}

img {
  object-fit: contain;
  max-width: 100%; /* Установка максимальной ширины картинки */
  height: auto; /* Автоматическое изменение высоты для сохранения пропорций */
}

h1,
h2,
h3,
h4,
p {
  margin: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

picture {
  font-size: 0;
}

.list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}

.btn-reset {
  padding: 0;
  border: none;
  background-color: transparent;
  cursor: pointer;
}

.d-flex {
  display: flex;
}

.d-flex-center {
  display: flex;
  align-items: center;
}

.d-flex-between {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
}

.d-flex-column {
