/* Variables */
.mobile-menu {
  display: none;
}
@media (max-width: 767.98px) {
  .mobile-menu {
    display: block;
  }
}

/* Menu Toggle */
.menu-toggle {
  display: block;
  position: relative;
  z-index: 1;
}
.menu-toggle__input {
  display: block;
  width: 24px;
  position: absolute;
  cursor: pointer;
  opacity: 0; /* hide this */
  z-index: 2; /* and place it over the hamburger */
  -webkit-touch-callout: none;
}
.menu-toggle__icon {
  display: block;
  width: 26px;
  height: 1px;
  margin-bottom: 6px;
  position: relative;
  background: #000;
  border-radius: 3px;
  z-index: 1;
  transform-origin: 4px 0px;
  transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1), background 0.5s cubic-bezier(0.77, 0.2, 0.05, 1), opacity 0.55s ease;
}
.menu-toggle__icon:first-child {
  transform-origin: 0% 0%;
}
.menu-toggle__icon:nth-last-child(2) {
  transform-origin: 0% 100%;
  margin-bottom: 0;
}
.menu-toggle__input:checked ~ .menu {
  transform: translate(2px, 2px);
}
.menu-toggle__input:checked ~ .menu-toggle__icon {
  opacity: 1;
  transform: rotate(45deg) translate(2px, 2px);
  background: #232323;
}
.menu-toggle__input:checked ~ .menu-toggle__icon:nth-last-child(3) {
  opacity: 0;
  transform: rotate(0deg) scale(0.2, 0.2);
}
.menu-toggle__input:checked ~ .menu-toggle__icon:nth-last-child(2) {
  transform: rotate(-45deg) translate(-4px, 2px);
}

/* Menu */
.menu {
  position: absolute;
  width: 100vw;
  min-height: 100vh;
  top: 34px;
  left: -18px;
  padding: 50px;
  padding-top: 125px;
  background: #fff;
  list-style-type: none;
  -webkit-font-smoothing: antialiased; /* to stop flickering of text in safari */
  transform-origin: 0% 0%;
  transform: translate(-150%, 0);
  transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1);
}
.menu__list {
  padding: 0 8px;
  margin: 0;
}
.menu__list__item {
  padding: 10px 0;
  font-size: 22px;
}

